@media screen and (max-width: 414px) {
    .menu__btn {
        margin-right: 10px; }

    #menu__toggle {
        opacity: 0; }

    .menu__btn {
        display: flex;
        align-items: center;
        position: absolute;
        top: 20px;
        left: 20px;
        width: 26px;
        height: 26px;
        cursor: pointer;
        z-index: 1; }

    .menu__btn > span,
    .menu__btn > span::before,
    .menu__btn > span::after {
        display: block;
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: #616161; }

    .menu__btn > span::before {
        content: '';
        top: -8px; }

    .menu__btn > span::after {
        content: '';
        top: 8px; }


    .menu__box {
        width: 180px !important; }


    .menu__item {
        display: block;
        padding: 0 0 0 0;
        color: #333;
        font-family: 'ProximaNova', sans-serif;
        font-size: 15px !important;
        font-weight: 600;
        text-decoration: none; }

    .menu {
        &__main {
            margin-top: 33px;
            padding: 25px 0 25px 0;
            width: 93px !important;
            height: auto; }
        &__—Åhildren {
            margin: 0;
            padding: 10px;
            width: 153px;
            height: auto; }
        &__bedroom {
            margin: 0;
            padding: 10px;
            width: 157px;
            height: auto; }
        &__kitchen {
            margin: 0;
            padding: 10px;
            width: 153px;
            height: auto; }
        &__contacts {
            margin: 0;
            padding: 10px;
            width: 153px;
            height: auto; } }




    #menu__toggle:checked ~ .menu__btn > span {
        transform: rotate(45deg); }

    #menu__toggle:checked ~ .menu__btn > span::before {
        top: 0;
        transform: rotate(0); }

    #menu__toggle:checked ~ .menu__btn > span::after {
        top: 0;
        transform: rotate(90deg); }

    #menu__toggle:checked ~ .menu__box {
        visibility: visible;
        left: 0; } }



